"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import { useData } from "@/lib/data-provider"
import { fetchCities } from "@/lib/services/data-service" // Imported service
import type { City } from "@/lib/db/countries" // Imported type
import { Header } from "@/components/ui/header"
import { Footer } from "@/components/ui/footer"
import { MobileNav } from "@/components/ui/mobile-nav"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Card, CardContent } from "@/components/ui/card"
import { Calendar, Weight, ChevronRight, ChevronLeft, Check, Plane, Info, Loader2 } from "@/components/icons"
import { cn } from "@/lib/utils"

const steps = [
  { id: 1, title: "Trajet", icon: Plane },
  { id: 2, title: "DÃ©tails", icon: Weight },
  { id: 3, title: "Confirmation", icon: Check },
]

export default function PublishPage() {
  const router = useRouter()
  const { currentUser, createTrip, countries } = useData()
  const [currentStep, setCurrentStep] = useState(1)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [formData, setFormData] = useState({
    departureCountry: "",
    departureCity: "",
    arrivalCountry: "",
    arrivalCity: "",
    departureDate: "",
    availableKg: "",
    pricePerKg: "",
    description: "",
    acceptedItems: "",
    rejectedItems: "",
  })

  // State for dynamic cities
  const [departureCities, setDepartureCities] = useState<City[]>([])
  const [arrivalCities, setArrivalCities] = useState<City[]>([])

  // Load departure cities when country changes
  useEffect(() => {
    const loadCities = async () => {
      const country = countries.find(c => c.name === formData.departureCountry)
      if (country) {
        const cities = await fetchCities(country.id)
        setDepartureCities(cities)
      } else {
        setDepartureCities([])
      }
    }
    loadCities()
  }, [formData.departureCountry, countries])

  // Load arrival cities when country changes
  useEffect(() => {
    const loadCities = async () => {
      const country = countries.find(c => c.name === formData.arrivalCountry)
      if (country) {
        const cities = await fetchCities(country.id)
        setArrivalCities(cities)
      } else {
        setArrivalCities([])
      }
    }
    loadCities()
  }, [formData.arrivalCountry, countries])

  const handleSubmit = async () => {
    if (!currentUser) {
      alert("Vous devez Ãªtre connectÃ© pour publier un trajet.")
      return
    }
    setIsSubmitting(true)
    try {
      const acceptedItems = formData.acceptedItems.split(',').map(item => item.trim()).filter(item => item.length > 0)
      const rejectedItems = formData.rejectedItems.split(',').map(item => item.trim()).filter(item => item.length > 0)

      const newTrip = await createTrip({
        userId: currentUser.id,
        departure: formData.departureCity,
        departureCountry: formData.departureCountry,
        arrival: formData.arrivalCity,
        arrivalCountry: formData.arrivalCountry,
        departureDate: formData.departureDate,
        availableKg: Number(formData.availableKg),
        pricePerKg: Number(formData.pricePerKg),
        description: formData.description,
        acceptedItems: acceptedItems,
        rejectedItems: rejectedItems,
      })

      if (newTrip) {
        alert("Trajet publiÃ© avec succÃ¨s !")
        router.push(`/trips/${newTrip.id}`)
      } else {
        throw new Error("La crÃ©ation du trajet a Ã©chouÃ©.")
      }
    } catch (error) {
      console.error(error)
      alert("Une erreur est survenue lors de la publication.")
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleNext = () => {
    if (currentStep < 3) {
      setCurrentStep(currentStep + 1)
    } else {
      handleSubmit()
    }
  }

  const handleBack = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1)
    }
  }

  const updateForm = (field: string, value: string) => {
    setFormData((prev) => ({ ...prev, [field]: value }))
  }

  if (!currentUser) {
    return (
      <div className="min-h-screen flex flex-col">
        <Header />
        <div className="flex-1 flex items-center justify-center">
          <div className="text-center">
            <h2 className="text-xl font-bold mb-2">AccÃ¨s refusÃ©</h2>
            <p className="text-muted-foreground mb-4">Vous devez Ãªtre connectÃ© pour publier un trajet.</p>
            <Link href="/login">
              <Button>Se connecter</Button>
            </Link>
          </div>
        </div>
        <MobileNav />
      </div>
    )
  }

  return (
    <div className="min-h-screen flex flex-col">
      <Header />
      <main className="flex-1 pb-20 md:pb-0 bg-secondary/20">
        <div className="container mx-auto px-4 py-8">
          <div className="max-w-2xl mx-auto">
            {/* Header */}
            <div className="text-center mb-8">
              <h1 className="text-2xl md:text-3xl font-bold mb-2">Publier un trajet</h1>
              <p className="text-muted-foreground">Partagez vos kilos disponibles avec la communautÃ©</p>
            </div>

            {/* Progress Steps */}
            <div className="flex items-center justify-center gap-4 mb-8">
              {steps.map((step, index) => (
                <div key={step.id} className="flex items-center">
                  <div
                    className={cn(
                      "flex items-center gap-2 px-4 py-2 rounded-full transition-colors",
                      currentStep >= step.id
                        ? "bg-primary text-primary-foreground"
                        : "bg-secondary text-muted-foreground",
                    )}
                  >
                    <step.icon className="h-4 w-4" />
                    <span className="text-sm font-medium hidden sm:inline">{step.title}</span>
                    <span className="text-sm font-medium sm:hidden">{step.id}</span>
                  </div>
                  {index < steps.length - 1 && <ChevronRight className="h-4 w-4 text-muted-foreground mx-2" />}
                </div>
              ))}
            </div>

            {/* Form Card */}
            <Card>
              <CardContent className="p-6 md:p-8">
                {/* Step 1: Route */}
                {currentStep === 1 && (
                  <div className="space-y-6">
                    <div className="grid sm:grid-cols-2 gap-6">
                      <div className="space-y-4">
                        <h3 className="font-semibold flex items-center gap-2">
                          <div className="w-3 h-3 rounded-full bg-accent" />
                          DÃ©part
                        </h3>
                        <div className="space-y-2">
                          <Label>Pays</Label>
                          <select
                            className="w-full h-10 px-3 rounded-lg border border-input bg-background"
                            value={formData.departureCountry}
                            onChange={(e) => updateForm("departureCountry", e.target.value)}
                          >
                            <option value="">SÃ©lectionner</option>
                            {countries.map((country) => (
                              <option key={country.code} value={country.name}>
                                {country.flag} {country.name}
                              </option>
                            ))}
                          </select>
                        </div>
                        <div className="space-y-2">
                          <Label>Ville</Label>
                          {departureCities.length > 0 ? (
                            <select
                              className="w-full h-10 px-3 rounded-lg border border-input bg-background"
                              value={formData.departureCity}
                              onChange={(e) => updateForm("departureCity", e.target.value)}
                            >
                              <option value="">SÃ©lectionner</option>
                              {departureCities.map((city) => (
                                <option key={city.id} value={city.name}>
                                  {city.name}
                                </option>
                              ))}
                            </select>
                          ) : (
                            <Input
                              placeholder="Ex: Paris"
                              value={formData.departureCity}
                              onChange={(e) => updateForm("departureCity", e.target.value)}
                              disabled={!formData.departureCountry}
                            />
                          )}
                        </div>
                      </div>

                      <div className="space-y-4">
                        <h3 className="font-semibold flex items-center gap-2">
                          <div className="w-3 h-3 rounded-full bg-foreground" />
                          ArrivÃ©e
                        </h3>
                        <div className="space-y-2">
                          <Label>Pays</Label>
                          <select
                            className="w-full h-10 px-3 rounded-lg border border-input bg-background"
                            value={formData.arrivalCountry}
                            onChange={(e) => updateForm("arrivalCountry", e.target.value)}
                          >
                            <option value="">SÃ©lectionner</option>
                            {countries.map((country) => (
                              <option key={country.code} value={country.name}>
                                {country.flag} {country.name}
                              </option>
                            ))}
                          </select>
                        </div>
                        <div className="space-y-2">
                          <Label>Ville</Label>
                          {arrivalCities.length > 0 ? (
                            <select
                              className="w-full h-10 px-3 rounded-lg border border-input bg-background"
                              value={formData.arrivalCity}
                              onChange={(e) => updateForm("arrivalCity", e.target.value)}
                            >
                              <option value="">SÃ©lectionner</option>
                              {arrivalCities.map((city) => (
                                <option key={city.id} value={city.name}>
                                  {city.name}
                                </option>
                              ))}
                            </select>
                          ) : (
                            <Input
                              placeholder="Ex: Dakar"
                              value={formData.arrivalCity}
                              onChange={(e) => updateForm("arrivalCity", e.target.value)}
                              disabled={!formData.arrivalCountry}
                            />
                          )}
                        </div>
                      </div>
                    </div>

                    <div className="space-y-2">
                      <Label className="flex items-center gap-2">
                        <Calendar className="h-4 w-4 text-muted-foreground" />
                        Date de dÃ©part
                      </Label>
                      <Input
                        type="date"
                        value={formData.departureDate}
                        onChange={(e) => updateForm("departureDate", e.target.value)}
                      />
                    </div>
                  </div>
                )}

                {/* Step 2: Details */}
                {currentStep === 2 && (
                  <div className="space-y-6">
                    <div className="grid sm:grid-cols-2 gap-6">
                      <div className="space-y-2">
                        <Label className="flex items-center gap-2">
                          <Weight className="h-4 w-4 text-muted-foreground" />
                          Kilos disponibles
                        </Label>
                        <Input
                          type="number"
                          placeholder="Ex: 15"
                          value={formData.availableKg}
                          onChange={(e) => updateForm("availableKg", e.target.value)}
                        />
                      </div>
                      <div className="space-y-2">
                        <Label>Prix par kilo (â‚¬)</Label>
                        <Input
                          type="number"
                          placeholder="Ex: 10"
                          value={formData.pricePerKg}
                          onChange={(e) => updateForm("pricePerKg", e.target.value)}
                        />
                      </div>
                    </div>

                    <div className="space-y-2">
                      <Label>Description (optionnel)</Label>
                      <Textarea
                        placeholder="DÃ©crivez votre trajet, les conditions de remise, etc."
                        rows={4}
                        value={formData.description}
                        onChange={(e) => updateForm("description", e.target.value)}
                      />
                    </div>

                    <div className="space-y-2">
                      <Label>Colis gÃ©nÃ©ralement acceptÃ©s (optionnel)</Label>
                      <Textarea
                        placeholder="Ex: VÃªtements, livres, documents, ..."
                        value={formData.acceptedItems}
                        onChange={(e) => updateForm("acceptedItems", e.target.value)}
                      />
                       <p className="text-xs text-muted-foreground">SÃ©parez les Ã©lÃ©ments par une virgule.</p>
                    </div>

                    <div className="space-y-2">
                      <Label>Colis refusÃ©s (optionnel)</Label>
                      <Textarea
                        placeholder="Ex: Alcool, produits illÃ©gaux, animaux, ..."
                        value={formData.rejectedItems}
                        onChange={(e) => updateForm("rejectedItems", e.target.value)}
                      />
                       <p className="text-xs text-muted-foreground">SÃ©parez les Ã©lÃ©ments par une virgule.</p>
                    </div>

                    <div className="p-4 bg-accent/10 rounded-lg flex items-start gap-3">
                      <Info className="h-5 w-5 text-accent mt-0.5" />
                      <p className="text-sm text-muted-foreground">
                        Soyez prÃ©cis dans votre description pour attirer plus de demandes. Indiquez les types de colis
                        acceptÃ©s et refusÃ©s.
                      </p>
                    </div>
                  </div>
                )}

                {/* Step 3: Confirmation */}
                {currentStep === 3 && (
                  <div className="space-y-6">
                    <div className="text-center py-4">
                      <div className="flex h-16 w-16 items-center justify-center rounded-full bg-accent/10 mx-auto mb-4">
                        <Check className="h-8 w-8 text-accent" />
                      </div>
                      <h3 className="text-xl font-semibold mb-2">RÃ©capitulatif</h3>
                      <p className="text-muted-foreground">VÃ©rifiez les informations de votre trajet</p>
                    </div>

                    <div className="bg-secondary/50 rounded-xl p-6 space-y-4">
                      <div className="flex items-center justify-between pb-4 border-b border-border">
                        <div className="flex items-center gap-2">
                          <div className="w-2 h-2 rounded-full bg-accent" />
                          <span className="font-medium">{formData.departureCity || "Ville de dÃ©part"}</span>
                        </div>
                        <span className="text-muted-foreground">â†’</span>
                        <div className="flex items-center gap-2">
                          <span className="font-medium">{formData.arrivalCity || "Ville d'arrivÃ©e"}</span>
                          <div className="w-2 h-2 rounded-full bg-foreground" />
                        </div>
                      </div>
                      <div className="grid grid-cols-3 gap-4 text-center">
                        <div>
                          <p className="text-sm text-muted-foreground">Date</p>
                          <p className="font-medium">{formData.departureDate || "-"}</p>
                        </div>
                        <div>
                          <p className="text-sm text-muted-foreground">Kilos</p>
                          <p className="font-medium">{formData.availableKg || "-"} kg</p>
                        </div>
                        <div>
                          <p className="text-sm text-muted-foreground">Prix</p>
                          <p className="font-medium">{formData.pricePerKg || "-"} â‚¬/kg</p>
                        </div>
                      </div>
                    </div>
                  </div>
                )}

                {/* Navigation Buttons */}
                <div className="flex items-center justify-between mt-8 pt-6 border-t border-border">
                  <Button variant="ghost" onClick={handleBack} disabled={currentStep === 1 || isSubmitting} className="gap-2">
                    <ChevronLeft className="h-4 w-4" />
                    Retour
                  </Button>
                  <Button onClick={handleNext} disabled={isSubmitting} className="gap-2 w-32">
                    {isSubmitting ? (
                      <Loader2 className="h-4 w-4 animate-spin" />
                    ) : currentStep === 3 ? (
                      "Publier"
                    ) : (
                      <>
                        Continuer
                        <ChevronRight className="h-4 w-4" />
                      </>
                    )}
                  </Button>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </main>
      <Footer />
      <MobileNav />
    </div>
  )
}
